.box-code {
  position: relative;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.box-code .name {
  height: 0.18rem;
  line-height: 0.18rem;
  font-size: 0.18rem;
  margin-bottom: 0.04rem;
}
.box-code .lan {
  height: 0.16rem;
  line-break: 0.16rem;
  font-size: 0.12rem;
  font-family: serif;
}
.box-code .desc {
  line-height: 0.22rem;
  font-size: 0.14rem;
}
.box-code .anticon {
  position: absolute;
  width: 0.24rem;
  height: 0.24rem;
  font-size: 0.24rem;
}
.box-code.text {
  padding: 0.32rem;
  background-color: #d7d2cb;
}
.box-code.text .lan {
  margin-bottom: 0.35rem;
}
.box-code.text .anticon {
  bottom: 0.32rem;
  left: 0.32rem;
}
.box-code.img {
  background-size: cover;
}
.box-code.img .name {
  position: absolute;
  bottom: 0.26rem;
  left: 0.16rem;
}
.box-code.img .lan {
  position: absolute;
  bottom: 0.1rem;
  left: 0.16rem;
}
.box-code.img .anticon {
  bottom: 0.2rem;
  right: 0.16rem;
}
