.article {
  margin: 1rem 0;
}
@media screen and (max-width: 750px) {
  .article {
    margin: 0.56rem 0;
  }
}
.article .loading-wrapper {
  padding: 2rem 0;
}
.article .main {
  margin: 0 auto;
  padding: 0.4rem 0.16rem;
  max-width: 10rem;
}
.article .main .tag {
  margin-bottom: 0.3rem;
  font-size: 0.2rem;
  color: #b6a269;
}
@media screen and (max-width: 750px) {
  .article .main .tag {
    margin-bottom: 0.2rem;
  }
}
.article .main .title {
  line-height: 0.7rem;
  font-size: 0.5rem;
  margin-bottom: 0.4rem;
}
@media screen and (max-width: 750px) {
  .article .main .title {
    line-height: 0.34rem;
    font-size: 0.3rem;
  }
}
.reader {
  font-size: 0.16rem;
}
.reader h1 {
  margin-top: 0.2rem;
  font-size: 0.36rem !important;
}
.reader h2 {
  margin-top: 0.2rem;
  font-size: 0.32rem !important;
}
.reader h3 {
  margin-top: 0.2rem;
  font-size: 0.24rem !important;
}
.reader h4 {
  margin-top: 0.2rem;
  font-size: 0.2rem !important;
}
.reader p {
  line-height: 0.26rem;
}
.reader ul {
  list-style: disc;
}
.reader ul li {
  line-height: 0.26rem;
  margin-left: 0.26rem;
}
.reader img {
  max-width: 100%;
}
.reader code {
  display: block;
}
.reader table th {
  text-align: left;
}
