.footer {
  position: relative;
  padding: 0.4rem;
  background: #000000;
  color: #b6a269;
  text-align: center;
}
.footer .logo {
  display: inline-block;
  margin-bottom: 0.1rem;
  width: 0.52rem;
  height: 0.52rem;
  background: url(/henry-site-react/build/static/media/logo_gold.e3691829.svg) no-repeat center center;
  background-size: 100% 100%;
}
.footer .more {
  margin-bottom: 0.1rem;
  height: 0.21rem;
  line-height: 0.21rem;
  font-size: 0.15rem;
  font-family: DINCond-Bold, sans-serif !important;
  letter-spacing: 0.03rem;
}
.footer .email {
  margin-bottom: 0.1rem;
  height: 0.16rem;
  line-height: 0.16rem;
  font-size: 0.12rem;
  font-family: sans-serif;
}
.footer .icons {
  font-size: 0.25rem;
}
.footer .top {
  position: absolute;
  top: 0.5rem;
  right: 1rem;
  cursor: pointer;
}
@media screen and (max-width: 750px) {
  .footer .top {
    display: none;
  }
}
.footer .top .anticon {
  font-size: 0.5rem;
}
