.cat-loading {
  position: relative;
  margin: 0 auto;
  width: 180px;
  height: 100px;
}
.cat-loading .cat-body {
  position: absolute;
  left: 56px;
  background: #000000;
  -webkit-animation: body 0.5s linear infinite alternate;
          animation: body 0.5s linear infinite alternate;
}
.cat-loading .cat-left-ear {
  position: absolute;
  left: 120px;
  width: 14px;
  height: 14px;
  background: #000000;
  border-radius: 4px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-animation: left-ear 0.5s linear infinite alternate;
          animation: left-ear 0.5s linear infinite alternate;
}
.cat-loading .cat-right-ear {
  position: absolute;
  left: 148px;
  width: 18px;
  height: 18px;
  background: #000000;
  border-radius: 4px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-animation: right-ear 0.5s linear infinite alternate;
          animation: right-ear 0.5s linear infinite alternate;
}
.cat-loading .cat-eye {
  position: absolute;
  top: 22px;
  left: 124px;
  width: 14px;
  height: 14px;
  background: #ffffff;
  border-radius: 50%;
}
.cat-loading .cat-eye .ring-1 {
  position: absolute;
  width: 9px;
  height: 9px;
  top: 2px;
  left: 4px;
  background: #e88f30;
  border-radius: 50%;
}
.cat-loading .cat-eye .ring-2 {
  position: absolute;
  width: 5px;
  height: 5px;
  top: 4px;
  left: 6px;
  background: #000000;
  border-radius: 50%;
}
.cat-loading .cat-eye .point {
  position: absolute;
  width: 3px;
  height: 3px;
  top: 2px;
  left: 4px;
  background: #ffffff;
  border-radius: 50%;
}
.cat-loading .cat-eye.cat-eye-left {
  -webkit-animation: left-eye 0.5s linear infinite alternate;
          animation: left-eye 0.5s linear infinite alternate;
}
.cat-loading .cat-eye.cat-eye-right {
  top: 22px;
  left: 150px;
  -webkit-animation: right-eye 0.5s linear infinite alternate;
          animation: right-eye 0.5s linear infinite alternate;
}
.cat-loading .cat-foot {
  position: absolute;
  top: 65px;
  left: 90px;
  width: 5px;
  height: 12px;
  background: #000000;
  border-radius: 2px;
}
.cat-loading .cat-foot.cat-foot-1 {
  left: 86px;
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
  -webkit-animation: foot-1 0.5s linear infinite alternate;
          animation: foot-1 0.5s linear infinite alternate;
}
.cat-loading .cat-foot.cat-foot-2 {
  left: 92px;
  -webkit-transform: rotate(-20deg);
          transform: rotate(-20deg);
  -webkit-animation: foot-2 0.5s linear infinite alternate;
          animation: foot-2 0.5s linear infinite alternate;
}
.cat-loading .cat-foot.cat-foot-3 {
  left: 136px;
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
  -webkit-animation: foot-1 0.5s linear infinite alternate;
          animation: foot-1 0.5s linear infinite alternate;
}
.cat-loading .cat-foot.cat-foot-4 {
  left: 142px;
  -webkit-transform: rotate(-20deg);
          transform: rotate(-20deg);
  -webkit-animation: foot-2 0.5s linear infinite alternate;
          animation: foot-2 0.5s linear infinite alternate;
}
.cat-loading .cat-tail {
  position: absolute;
  top: 17px;
  width: 70px;
  height: 18px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 20px;
  border-top-right-radius: 60px 20px;
  border-bottom-right-radius: 80px 10px;
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
  -webkit-transform-origin: right;
          transform-origin: right;
  background: #000000;
  -webkit-animation: tail 0.5s linear infinite alternate;
          animation: tail 0.5s linear infinite alternate;
}
.cat-loading .cat-shadow-body {
  position: absolute;
  top: 76px;
  left: 56px;
  width: 120px;
  height: 16px;
  background: #b6a269;
  border-radius: 50%;
  -webkit-animation: shadow-body 0.5s linear infinite alternate;
          animation: shadow-body 0.5s linear infinite alternate;
}
.cat-loading .cat-shadow-tail {
  position: absolute;
  top: 78px;
  left: 0;
  width: 60px;
  height: 8px;
  background: #b6a269;
  border-top-left-radius: 14px;
  border-bottom-left-radius: 6px;
  border-top-right-radius: 60px 15px;
  border-bottom-right-radius: 60px 5px;
  -webkit-animation: shadow-tail 0.5s linear infinite alternate;
          animation: shadow-tail 0.5s linear infinite alternate;
}
@-webkit-keyframes body {
  from {
    top: 12px;
    width: 116px;
    height: 60px;
    border-top-left-radius: 50px 30px;
    border-top-right-radius: 10px 30px;
    border-bottom-right-radius: 40px 30px;
    border-bottom-left-radius: 40px 30px;
  }
  to {
    top: 14px;
    width: 118px;
    height: 58px;
    border-top-left-radius: 40px 34px;
    border-top-right-radius: 15px 26px;
    border-bottom-right-radius: 60px 34px;
    border-bottom-left-radius: 60px 26px;
  }
}
@keyframes body {
