.about {
  padding-top: 1rem;
}
.about .head {
  padding: 0.5rem 0.8rem;
}
@media screen and (max-width: 750px) {
  .about .head {
    padding: 0.5rem 0.4rem;
  }
}
.about .head .page {
  text-align: center;
}
.about .head .page .title {
  margin-bottom: 0.4rem;
  line-height: 0.68rem;
  font-size: 0.6rem;
  color: #000000;
}
@media screen and (max-width: 750px) {
  .about .head .page .title {
    line-height: 0.34rem;
    font-size: 0.3rem;
  }
}
.about .head .page .link {
  color: #b6a269;
  cursor: pointer;
  text-decoration: underline;
}
.about .head .page .text {
  margin-bottom: 0.4rem;
}
.about .time-line {
  display: flex;
  align-items: stretch;
  padding: 0.4rem;
  background: #000;
}
.about .time-line .center-line {
  flex-shrink: 0;
  position: relative;
  width: 0.04rem;
  background: #b6a269;
}
.about .time-line .center-line:before,
.about .time-line .center-line:after {
  content: '';
  position: absolute;
  left: -0.09rem;
  display: block;
  width: 0.22rem;
  height: 0.22rem;
  border-radius: 50%;
  background: #b6a269;
}
.about .time-line .center-line:before {
  top: -0.09rem;
}
.about .time-line .center-line:after {
  bottom: -0.09rem;
}
.about .time-line .left {
  flex-grow: 1;
  padding-top: 0.3rem;
}
.about .time-line .right {
  flex-grow: 1;
  padding-top: 0.7rem;
}
.about .edu .left {
  background: url('http://www.ustb.edu.cn/images/content/2017-04/20170422132609105693.jpg') no-repeat center center;
  background-size: cover;
}
@media screen and (max-width: 450px) {
  .about .edu .left {
    padding-bottom: 100%;
  }
}
.about .edu .right {
  padding: 0.5rem 0;
}
.time-line-box {
  display: flex;
  margin-bottom: 0.6rem;
  color: #b6a269;
}
.time-line-box.left {
  flex-direction: row-reverse;
  text-align: right;
}
.time-line-box.left .content-wrapper {
  margin-right: 0.26rem;
}
.time-line-box.right .content-wrapper {
  margin-left: 0.26rem;
}
.time-line-box .line {
  flex-shrink: 0;
  width: 0.34rem;
  height: 0.04rem;
  margin-top: 0.3rem;
  background: #b6a269;
}
.time-line-box .logo {
  flex-shrink: 0;
  width: 0.7rem;
  height: 0.66rem;
  border-radius: 0.12rem;
  background-color: #ebe8e5;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 0.5rem auto;
}
.time-line-box .logo.didi {
  background-image: url(data:image/png;base64,);
}
.time-line-box .logo.baidu {
  background-image: url(data:image/png;base64,);
}
.time-line-box .logo.tieba {
  background-image: url(data:image/png;base64,);
}
.time-line-box .logo.dd {
  background-image: url(/henry-site-react/build/static/media/dd.da548797.png);
}
.time-line-box .logo.tj {
  background-image: url(/henry-site-react/build/static/media/tj.aa746999.png);
}
.time-line-box .content-wrapper .name {
  line-height: 0.4rem;
  font-size: 0.3rem;
  color: #b6a269;
}
.time-line-box .content-wrapper .time {
  margin-bottom: 0.1rem;
  line-height: 0.26rem;
  font-size: 0.16rem;
  color: #b6a269;
}
.time-line-box .content-wrapper .desc {
  line-height: 0.19rem;
  font-size: 0.14rem;
  color: #ebe8e5;
}
