.top-bar {
  position: fixed;
  z-index: 1000;
  display: flex;
  width: 100%;
  height: 0.82rem;
  padding-top: 0.05rem;
  justify-content: space-between;
  align-items: center;
  background: transparent;
  -webkit-transition: background-color 0.8s, opacity 0.8s;
  transition: background-color 0.8s, opacity 0.8s;
}
.top-bar .logo {
  font-size: 0.28rem;
  margin-left: 0.5rem;
  letter-spacing: 0.06rem;
  color: #2e2925;
  font-family: DINCond-Bold, sans-serif;
  -webkit-transition: color 0.8s;
  transition: color 0.8s;
}
.top-bar .menu-btn {
  position: relative;
  margin-right: 0.5rem;
  padding: 0.1rem;
  width: 0.2rem;
  height: 0.17rem;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  cursor: pointer;
}
.top-bar .menu-btn span {
  display: block;
  width: 100%;
  height: 0.03rem;
  margin-top: 0.07rem;
  background: #2e2925;
  -webkit-transition: background-color 0.8s;
  transition: background-color 0.8s;
}
.top-bar .menu-btn span:before,
.top-bar .menu-btn span:after {
  position: absolute;
  content: '';
  width: 0.2rem;
  height: 0.03rem;
  background: #2e2925;
  -webkit-transition: background-color 0.8s;
  transition: background-color 0.8s;
}
.top-bar .menu-btn span:before {
  top: 0.1rem;
}
.top-bar .menu-btn span:after {
  bottom: 0.1rem;
}
.top-bar .menu-btn:hover {
  -webkit-transform: scaleY(1.2);
          transform: scaleY(1.2);
}
.top-bar.active {
  background: #000;
  opacity: 0.9;
}
.top-bar.active .logo {
  color: #b6a269;
}
.top-bar.active .menu-btn span {
  background: #b6a269;
}
.top-bar.active .menu-btn span:before,
.top-bar.active .menu-btn span:after {
  background: #b6a269;
}
.top-bar.hidden {
  opacity: 0;
  pointer-events: none;
}
@media screen and (max-width: 750px) {
  .top-bar {
    padding-top: 0;
    padding-bottom: 0.05rem;
    height: 0.52rem;
    background: #000;
    opacity: 0.9;
  }
  .top-bar .logo {
    font-size: 0.2rem;
    margin-left: 0.3rem;
    letter-spacing: 0.04rem;
    color: #b6a269;
  }
  .top-bar .menu-btn {
    margin-right: 0.3rem;
  }
  .top-bar .menu-btn span {
    background: #b6a269;
  }
  .top-bar .menu-btn span:before,
  .top-bar .menu-btn span:after {
    background: #b6a269;
  }
  .top-bar.hidden {
    opacity: 0;
  }
}
