@font-face {
  font-family: DINCond-Bold;
  src: url(/henry-site-react/build/static/media/DINCond-Bold.7aa49b14.otf);
}
html {
  font-size: 625%;
}
body {
  background: #ebe8e5;
  margin: 0;
  padding: 0;
}
body a {
  color: inherit;
  text-decoration: none;
}
body ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
body h1,
body h2,
body h3,
body h4,
body p {
  margin: 0;
  font-weight: 400;
}
@media screen and (min-width: 750px) {
  h1,
  h2 {
    font-size: 0.68rem;
  }
}
@media screen and (max-width: 750px) {
  h1,
  h2 {
    font-size: 0.34rem;
  }
}
body {
  font-size: 0.16rem;
  font-family: PingFangSC-Light, "\5FAE\8F6F\96C5\9ED1", "Hiragino Sans GB", arial;
}
body.clip {
  overflow: hidden;
}
h3 {
  font-size: 0.2rem;
}
p {
  font-size: 0.16rem;
}
.gold {
  color: #b6a269;
}
.btn-wrapper {
  display: block;
  margin-top: 0.4rem;
  text-align: center;
}
.btn {
  display: inline-block;
  width: 2.14rem;
  height: 0.46rem;
  margin: 0.15rem;
  border: 0.03rem solid #000000;
  line-height: 0.46rem;
  font-size: 0.2rem;
  text-align: center;
  -webkit-transition: background-color 0.2s, color 0.2s;
  transition: background-color 0.2s, color 0.2s;
}
.btn:hover,
.btn.emb {
  background: #000000;
  color: #ffffff;
}
.btn.btn-en {
  font-family: DINCond-Bold, sans-serif !important;
  letter-spacing: 0.03rem;
}
.btn.btn-black {
  border-color: #ffffff;
  color: #ffffff;
}
.btn.btn-black:hover,
.btn.btn-black.emb {
  background: #ffffff;
  color: #000000;
}
.block-2-col {
  display: flex;
  align-items: stretch;
  flex-wrap: wrap;
  padding: 0.1rem 0;
  font-size: 0;
}
.block-2-col .col {
  width: 50%;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
}
@media screen and (max-width: 450px) {
  .block-2-col .col {
    width: 100%;
  }
}
.block-2-col .col p {
  width: 100%;
}
.text-gold {
  color: #b6a269 !important;
}
.text-topic {
  margin: 0.03rem 0 0.2rem;
