.gallery {
  margin-top: 1rem;
  font-family: PingFangSC-Thin, "\5FAE\8F6F\96C5\9ED1", "Hiragino Sans GB", arial;
}
@media screen and (max-width: 750px) {
  .gallery {
    margin-top: 0.56rem;
  }
}
.gallery-header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 4rem;
  box-sizing: border-box;
  padding: 0.4rem;
  color: #ffffff;
  text-align: center;
  background: url(/henry-site-react/build/static/media/bg.04549e82.jpg) no-repeat center bottom;
  background-size: cover;
}
.gallery-header .gh-title {
  height: 0.4rem;
  margin-bottom: 0.4rem;
  line-height: 0.4rem;
  font-size: 0.34rem;
}
.gallery-wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-size: 0;
  padding-top: 0.1rem;
}
.gallery-wrapper .img-box {
  position: relative;
  display: inline-block;
  margin-bottom: 0.1rem;
  cursor: pointer;
}
.gallery-wrapper .img-box:hover .img-mask {
  opacity: 1;
}
.gallery-wrapper .img-box .img-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 0.5rem;
  background: #000000;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
  opacity: 0;
}
.gallery-wrapper .img-box .img-mask .img-mask-tag {
  margin: 0.03rem 0 0.3rem;
  line-height: 0.25rem;
  font-size: 0.18rem;
  color: #b6a269;
  text-align: center;
}
.gallery-wrapper .img-box .img-mask .img-mask-title {
  line-height: 0.33rem;
  font-size: 0.28rem;
  color: #ffffff;
  text-align: center;
}
