.box-blog {
  position: relative;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 0.38rem;
  cursor: pointer;
  overflow: hidden;
}
.box-blog .tag {
  margin-bottom: 0.25rem;
  line-height: 0.14rem;
  font-size: 0.14rem;
}
.box-blog .title {
  margin-bottom: 0.35rem;
  line-height: 0.33rem;
  font-size: 0.28rem;
}
.box-blog .desc {
  margin-bottom: 0.1rem;
  line-height: 0.19rem;
  font-size: 0.12rem;
}
.box-blog .hover {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.box-blog:hover .hover {
  opacity: 1;
}
.box-blog.dark {
  background: #000000;
}
.box-blog.dark .tag {
  color: #b6a269;
}
.box-blog.dark .title,
.box-blog.dark .desc {
  color: #ffffff;
}
.box-blog.light {
  background: #ffffff;
}
.box-blog.light .tag {
  color: #b6a269;
}
.box-blog.light .title,
.box-blog.light .desc {
  color: #000000;
}
.box-blog.gold {
  background: #b6a269;
}
.box-blog.gold .tag {
  color: #000000;
}
.box-blog.gold .title,
.box-blog.gold .desc {
  color: #ffffff;
}
