.loader {
  position: relative;
  width: 1.3rem;
  margin: 0 auto;
  cursor: pointer;
}
.loader .coffee {
  position: absolute;
  z-index: 1;
  top: 0.08rem;
  right: 0.08rem;
  bottom: 0.08rem;
  left: 0.08rem;
  background: #ebe8e5;
  border-radius: 0 0 0.08rem 0.08rem;
  overflow: hidden;
}
.loader .coffee.loading .coffee-1 {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0.7rem;
  height: 0.65rem;
  border-top-left-radius: 50% 0.2rem;
  border-top-right-radius: 50% 0.2rem;
  background: #b6a269;
  box-shadow: -0.7rem 0 0 0 #b6a269, 0.7rem 0 0 0 #b6a269;
  -webkit-animation: run1 0.6s linear infinite;
          animation: run1 0.6s linear infinite;
}
@-webkit-keyframes run1 {
  from {
    left: 0;
  }
  to {
    left: 0.7rem;
  }
}
@keyframes run1 {
  from {
    left: 0;
  }
  to {
    left: 0.7rem;
  }
}
.loader .coffee.loading .coffee-2 {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0.8rem;
  height: 0.55rem;
  border-top-left-radius: 50% 0.2rem;
  border-top-right-radius: 50% 0.2rem;
  background: #2e2925;
  box-shadow: -0.8rem 0 0 0 #2e2925, 0.8rem 0 0 0 #2e2925;
  -webkit-animation: run2 0.4s linear infinite;
          animation: run2 0.4s linear infinite;
}
@-webkit-keyframes run2 {
  from {
    left: 0;
  }
  to {
    left: 0.8rem;
  }
}
@keyframes run2 {
  from {
    left: 0;
  }
  to {
    left: 0.8rem;
  }
}
.loader .coffee.failed .retry {
  position: relative;
  margin: 0.3rem auto;
  width: 0.4rem;
  height: 0.4rem;
  border: 0.08rem solid #2e2925;
  border-radius: 50%;
}
.loader .coffee.failed .retry:before {
  content: '';
  position: absolute;
  left: 0.2rem;
  bottom: 0.2rem;
  width: 0.5rem;
  height: 0.5rem;
  background: #ebe8e5;
}
.loader .coffee.failed .retry:after {
  content: '';
  position: absolute;
  left: 0.2rem;
  top: -0.14rem;
  width: 0;
  height: 0;
  border: 0.1rem solid;
  border-color: transparent transparent transparent #2e2925;
}
.loader .cup {
  position: relative;
  width: 1.3rem;
  height: 1.3rem;
  background: #2e2925;
  border-radius: 0 0 0.14rem 0.14rem;
}
.loader .cup:before {
  content: '';
  position: absolute;
  top: 0.36rem;
  left: 1.22rem;
  width: 0.3rem;
  height: 0.46rem;
  background: #ebe8e5;
  border: 0.08rem solid #2e2925;
  border-radius: 0 0.2rem 0.7rem 0;
}
.loader .cup:after {
  content: '';
  position: absolute;
  bottom: -0.08rem;
  left: -0.08rem;
  right: -0.08rem;
  height: 0.08rem;
  background: #2e2925;
  border-radius: 0.04rem;
}
