.box {
  padding-right: 0.1rem;
  box-sizing: border-box;
  margin-bottom: 10px;
}
@media screen and (max-width: 450px) {
  .box {
    width: 100%;
  }
}
@media screen and (max-width: 800px) and (min-width: 450px) {
  .box {
    width: 50%;
  }
}
@media screen and (max-width: 1120px) and (min-width: 800px) {
  .box {
    width: 33.33%;
  }
}
@media screen and (max-width: 1440px) and (min-width: 1120px) {
  .box {
    width: 25%;
  }
}
@media screen and (max-width: 1760px) and (min-width: 1440px) {
  .box {
    width: 20%;
  }
}
@media screen and (min-width: 1760px) {
  .box {
    width: 16.66%;
  }
}
.box .box-holder {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
}
.box .box-holder .box-inner {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.box .box-holder::after {
  position: absolute;
  z-index: 0;
  bottom: 50%;
  left: 50%;
  width: 0.2rem;
  height: 0.2rem;
  margin-left: -0.1rem;
  content: '';
  display: block;
  border-radius: 50%;
  border: 0.02rem solid;
  border-color: #000000 transparent transparent transparent;
  -webkit-animation: roll 0.8s linear infinite;
          animation: roll 0.8s linear infinite;
}
@-webkit-keyframes roll {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes roll {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
