.blog {
  margin-top: 1rem;
}
@media screen and (max-width: 750px) {
  .blog {
    margin-top: 0.56rem;
  }
}
.blog .filter-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 0.14rem;
  font-size: 0.16rem;
  line-height: 0.22rem;
}
@media screen and (max-width: 750px) {
  .blog .filter-wrapper {
    display: none;
  }
}
.blog .filter-wrapper .filter-title {
  flex-shrink: 0;
  color: #b6a269;
  margin: 0 0.12rem;
  width: 0.8rem;
}
.blog .filter-wrapper .filter-list {
  display: flex;
  flex-wrap: wrap;
}
.blog .filter-wrapper .filter-list .filter-item {
  margin: 0 0.24rem;
  margin-bottom: 0.1rem;
  color: #2e2925;
  border-bottom: 1px solid transparent;
  white-space: nowrap;
  cursor: pointer;
}
.blog .filter-wrapper .filter-list .filter-item:hover,
.blog .filter-wrapper .filter-list .filter-item.current {
  border-color: #2e2925;
}
