.nav {
  opacity: 0;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
  pointer-events: none;
  position: fixed;
  z-index: 1001;
  top: 0;
  right: 0;
  left: 0;
  box-sizing: border-box;
  background: #000;
  overflow: hidden;
}
.nav .top-bar .logo {
  color: #b6a269;
}
.nav .top-bar .menu-btn span {
  background: #b6a269;
}
.nav .top-bar .menu-btn span:before,
.nav .top-bar .menu-btn span:after {
  -webkit-transform-origin: right;
          transform-origin: right;
  background: #b6a269;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.nav.active {
  opacity: 1;
  pointer-events: all;
}
.nav.active .top-bar .menu-btn {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.nav.active .top-bar .menu-btn span {
  background: transparent;
}
.nav.active .top-bar .menu-btn span:before {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.nav.active .top-bar .menu-btn span:after {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.nav .link-list {
  display: flex;
  justify-content: center;
  align-items: stretch;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  min-height: 5rem;
}
.nav .link-list li {
  text-align: center;
}
.nav .link-list a {
  font-family: DINCond-Bold, sans-serif;
  letter-spacing: 0.03rem;
  font-size: 0.7rem;
  line-height: 0.9rem;
  color: #ffffff;
}
.nav .link-list a:hover {
  color: #b6a269;
}
@media screen and (max-width: 750px) {
  .nav .link-list {
    min-height: 0;
  }
  .nav .link-list a {
    font-size: 0.5rem;
  }
}
